
import Clipboard from 'clipboard'
import Tianmao from './tianmao.svg'
import Taobao from './taobao.svg'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: 'appendCommon',
components: {
Toast
},
data () {
return {
Taobao: Taobao,
Tianmao: Tianmao
}
},
methods: {
doCopy () {
var clipboard = new Clipboard('.btn')
clipboard.on('success', (e) => {
this.$vux.toast.text('复制成功!', 'middle')
})
}
},
props: {
goodsObj: {
type: Object,
default: {}
}
}
}



